.Linktree{
    position: relative;
}
.Btn-wrapper{
    width: 90%;
    max-width: 72em;
    margin: 3em auto;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.Toggle{
    position: absolute;
    right: 2em;
    top: -2em;
    width: 1em;
    height: 1em;
    padding: .5em;
    border-radius: 50%;
    border: 1px dashed #D0D5DD;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
#Back{
display: none;
}

#contact{
    font-weight: 600;
    font-size: .599rem;
}
@media only screen and (min-width: 45em){
    #Open{
        display: none;
    }
    #Back{
        display: block;
    }
    .Toggle{
        width: 2em;
        height: 2em;
        right: 20%;
        top: 6%;
    }
}
#contact{
    font-size: 1.099rem;
}